import React from 'react';
import { Link } from '@webui-one/router';
import { isNativeApplication } from '@webui-one/core/defaultApplications';
import PropTypes from 'prop-types';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import style from './ApplicationIconStyles';
import BoxIcon from './static/BoxIcon';

const ApplicationIcon = (props) => {
    const { href } = props;
    const native = isNativeApplication(props);
    let link;

    if (native) {
        link = (
            <Link
                to={{
                    pathname: href,
                    state: {
                        applicationHub: true,
                    },
                }}
                style={style.native}
                data-application-type="native"
            >
                <div style={style.logo} />
                <div style={style.title}>
                    {props.title}
                </div>
                <div style={style.description}>
                    {props.description}
                </div>
                {props.promoLabel &&
                    <div data-handle="promoLabel" style={style.promoLabel}>{props.promoLabel}</div>
                    }
            </Link>
        );
    } else {
        const packageType = props.package === true ? 'package' : 'custom';
        link = (
            <Link href={href} style={style.applicationIcon} data-application-type={packageType}>
                <div style={style.logo} />
                <div
                    data-handle="applicationTitle"
                    style={props.package ? style.titlePackage : style.title}
                >
                    {props.package && (
                        <div style={style.icon}>
                            <BoxIcon color={defaultStyles.color9} />
                        </div>
                    )}
                    {props.title}
                </div>
                <div style={style.description}>
                    {props.description}
                </div>
                {props.promoLabel &&
                    <div data-handle="promoLabel" style={style.promoLabel}>{props.promoLabel}</div>
                    }
            </Link>
        );
    }
    return (
        <div data-handle="applicationIcon" style={style.wrapper} key={href}>
            { link }
        </div>
    );
};

ApplicationIcon.propTypes = {
    description: PropTypes.string,
    href: PropTypes.string.isRequired,
    title: PropTypes.string.isRequired,
    promoLabel: PropTypes.string,
    package: PropTypes.bool,
};

export default ApplicationIcon;
