import PropTypes from 'prop-types';
import React from 'react';
import Loader from '@webui-one/components/Loading/Loader';
import { isDevEnv } from '@webui-one/core/utils/platform';
import * as defaultStyles from '@webui-one/styles/ApplicationDefaultStyles';
import FooterContainer from 'Common/Components/Footer/FooterContainer';
import HeaderContainer from 'Common/Components/Header/HeaderContainer';
import LastLoginNotificationBar from 'Common/Components/Notification/LastLoginNotificationBar';
import ApplicationGrid from './ApplicationGrid';
import style from './ApplicationHubStyles';

const buildType = () => (isDevEnv() ? 'development build' : 'release build');

const ApplicationHub = ({ categories, isLoading, isLoadingPackageApplications }) => {
    const applicationCategories = categories.map(category => (
        <ApplicationGrid {...category} />
    ));
    return (
        <div style={style.application}>
            <HeaderContainer >
                <LastLoginNotificationBar />
                <div style={defaultStyles.shadow} />
            </HeaderContainer>
            <div style={style.wrapper}>
                <div style={style.applications}>
                    {isLoading && <Loader color={style.loader.color} />}
                    {!isLoading && applicationCategories}
                    {!isLoading && isLoadingPackageApplications &&
                        <Loader color={style.loader.color} extraStyle={{ marginBottom: '1rem' }} />
                    }
                    <div style={style.version}>
                        {`webui one, ${buildType()}`}
                    </div>
                </div>
            </div>
            <FooterContainer />
        </div>
    );
};

ApplicationHub.propTypes = {
    categories: PropTypes.arrayOf(PropTypes.shape).isRequired,
    isLoading: PropTypes.bool,
    isLoadingPackageApplications: PropTypes.bool,
};

export default ApplicationHub;
