import { useEffect, useState, useCallback } from 'react';
import { useTransactionJsonRpc } from '@webui-one/components/hooks/useTransactionJsonRpc';
import {
    query as _query,
} from '@webui-one/core/jsonrpc';
import defaultApplications from '@webui-one/core/defaultApplications';
import { ERROR_INFO_SELECTOR } from 'modules/packages/paths';
import { formatAppHref } from '../applications/actions';


const UI_PACKAGE_SELECTION = [
    'name',
    'description',
    'package-version',
    ERROR_INFO_SELECTOR,
];

const WEBUI_APPLICATIONS_SELECTION = [
    'id',
    'category',
    'href',
    'title',
    'description',
    'promo-label',
    'abbreviation',
    'shortcut',
    'hidden',
];

const mapSelectionToResult = (selection, results) => results.map(res =>
    selection.reduce((acc, sel, index) => ({
        ...acc,
        [sel]: res[index],
    }), {}));

export function useFetchApplications({
    trigger = 0,
    getLastTh,
    funcs: {
        query = _query,
    } = {},
} = {}) {
    const [applications, setApplications] = useState(defaultApplications);

    const state = useTransactionJsonRpc({
        trigger,
        asyncFunc: useCallback(async ({ signal, th }) => query({
            signal,
            th,
            path: '/webui:webui/webui-one:applications/application',
            selection: WEBUI_APPLICATIONS_SELECTION,
        }), [query]),

        processResultFunc: useCallback(results =>
            mapSelectionToResult(WEBUI_APPLICATIONS_SELECTION, results)
                .reduce((acc, result) => ({
                    ...acc,
                    [result.id]: {
                        ...result,
                        href: formatAppHref(result.href),
                    },
                }), {}), []),

        getLastTh,
    });

    useEffect(() => {
        setApplications({
            ...defaultApplications,
            ...state.data,
        });
    }, [state.data]);

    return { ...state, data: applications };
}

export function useFetchUIPackages({
    packageAppNames,
    getLastTh,
    funcs: {
        query = _query,
    } = {},
} = {}) {
    const createXpath = (names) => {
        const nameXpath = names.map(name => `name="${name}"`).join(' or ');
        return `/ncs:packages/package[${nameXpath}]`;
    };

    return useTransactionJsonRpc({
        asyncFunc: useCallback(async ({ signal, th }) => {
            if (packageAppNames.length > 0) {
                return query({
                    signal,
                    th,
                    xpathExpr: createXpath(packageAppNames),
                    selection: UI_PACKAGE_SELECTION,
                });
            }
            return {};
        }, [query, packageAppNames]),

        processResultFunc: useCallback(results =>
            mapSelectionToResult(UI_PACKAGE_SELECTION, results)
                .reduce((acc, curr) => ({
                    ...acc,
                    [curr.name]: {
                        ...curr,
                        webui: true,
                    },
                }), {}), []),

        getLastTh,
    });
}
