import { useEffect, useState } from 'react';
import { getLastTh as _getLastTh } from '@webui-one/core/jsonrpc';

const initialState = {
    isLoading: false,
    data: null,
    error: null,
};

export const useTransactionJsonRpc = ({
    trigger,
    asyncFunc,
    processResultFunc,
    getLastTh = _getLastTh,
} = {}) => {
    const [state, setState] = useState(initialState);

    useEffect(() => {
        const controller = new AbortController();
        const signal = controller.signal;
        const updateState = (newState) => {
            setState(prevState => ({ ...prevState, ...newState }));
        };

        async function fetch() {
            try {
                updateState({ isLoading: true });
                const th = await getLastTh({ signal });
                const response = await asyncFunc({ signal, th });

                if (!signal.aborted) {
                    const data = processResultFunc(response.results);
                    updateState({ isLoading: false, data });
                }
            } catch (error) {
                if (!signal.aborted) {
                    updateState({ isLoading: false, error });
                }
            }
        }
        fetch();

        return () => {
            controller.abort();
        };
    }, [asyncFunc, getLastTh, processResultFunc, setState, trigger]);

    return state;
};

export {
    initialState as _initialState,
};
