import React from 'react';
import PropTypes from 'prop-types';
import ApplicationIcon from './ApplicationIcon';
import style from './ApplicationGridStyles';

const ApplicationGrid = (props) => {
    const { applicationItems, title } = props;
    const applicationElements = applicationItems.map(appProps => (
        <ApplicationIcon key={appProps.id} {...appProps} />
    ));
    return (
        <div style={style.gridBox}>
            <div style={style.header}>{title.toUpperCase()}</div>
            <div style={style.grid}>
                {applicationElements}
            </div>
            <div style={style.clearFix} />
        </div>
    );
};

ApplicationGrid.propTypes = {
    applicationItems: PropTypes.arrayOf(PropTypes.object).isRequired,
    title: PropTypes.string.isRequired,
};

export default ApplicationGrid;
