import React from 'react';
import PropTypes from 'prop-types';
import styles from './Styles';


export const BAR_TYPES = {
    ALARM: 'alarm',
    ERROR: 'error',
    NOTIFICATION: 'notification',
    WARNING: 'warning',
};

const ENUM_PROP = Object.values(BAR_TYPES);
const DEFAULT_STYLE = {};

const NotificationBar = ({ message, type, additionalStyles = DEFAULT_STYLE }) => {
    if (!message) return null;
    return (
        <div data-handle={`${type}-bar`} style={{ ...styles[type], ...additionalStyles }}>
            {message}
        </div>
    );
};

NotificationBar.propTypes = {
    message: PropTypes.string,
    additionalStyles: PropTypes.shape({}),
    type: PropTypes.oneOf(ENUM_PROP),
};

NotificationBar.defaultProps = {
    type: BAR_TYPES.NOTIFICATION,
};

export default NotificationBar;
