import {
    useCallback,
    useEffect as _useEffect,
    useMemo,
} from 'react';

import {
    useDispatch as _useDispatch,
    useSelector,
} from 'react-redux';

import _useThunk from '@webui-one/components/hooks/useThunk';
import _useTimeout from '@webui-one/components/hooks/useTimeout';

import { selectLastLoginDisplayed } from 'modules/application/selectors';
import { lastLoginDisplayedAction } from 'modules/application/actions';

import {
    query as queryThunk,
} from 'modules/nodes/actions';

import { selectRows } from 'modules/nodes/selectors';
import { selectNsoUser } from 'modules/systemSetting/reducer';


const getLastLoginThunk = user => async (dispatch) => {
    const path = `/last:last-logins/user{${user}}/login`;
    const selection = [
        'login-time',
        'authenticating-host',
        'client-host',
        'transport',
        'num-of-failed-logins',
    ];
    return dispatch(queryThunk({ path, selection }));
};

export const useSelectLastLoginDisplayed = () => useSelector(selectLastLoginDisplayed);
export const useSelectLastLoginRows = user => (
    useSelector(state => selectRows(state, `/last:last-logins/user{${user}}/login`))
);

export const useSelectNsoUser = () => useSelector(selectNsoUser);

export const useDispatchGetLastLogin = ({
    user,
    displayed,
    hooks: {
        useEffect = _useEffect,
        useThunk = _useThunk,
    } = {},
} = {}) => {
    const getLastLoginUser = useMemo(() => getLastLoginThunk(user), [user]);
    const getLastLogin = useThunk(getLastLoginUser);

    useEffect(() => {
        if (!displayed && user) getLastLogin();
    }, [displayed, getLastLogin, user]);
};

export const useDispatchLastLoginDisplayed = ({
    displayed,
    displayTime,
    hooks: {
        useDispatch = _useDispatch,
        useEffect = _useEffect,
        useTimeout = _useTimeout,
    } = {},
} = {}) => {
    const dispatch = useDispatch();

    const callback = useCallback(() => {
        if (!displayed) {
            dispatch(lastLoginDisplayedAction());
        }
    }, [displayed, dispatch]);

    useTimeout(callback, displayTime);

    useEffect(() => callback, [callback, displayed, dispatch]);
};
