import React from 'react';
import PropTypes from 'prop-types';

import { toNumber } from '@webui-one/core/utils/number';

import NotificationBar, { BAR_TYPES } from '@webui-one/components/Notification/NotificationBar';

import {
    useDispatchGetLastLogin as _useDispatchGetLastLogin,
    useDispatchLastLoginDisplayed as _useDispatchLastLoginDisplayed,
    useSelectNsoUser as _useSelectNsoUser,
    useSelectLastLoginDisplayed as _useSelectLastLoginDisplayed,
    useSelectLastLoginRows as _useSelectLastLoginRows,
} from './hooks';


const getLoginTime = (row) => {
    const loginTimeString = row['login-time'];
    return loginTimeString.split('T')[0];
};

const getNotification = (user, rows) => {
    if (rows.length < 2) return null;
    const last = rows[1];
    const loginTime = getLoginTime(last);
    const authenticatingHost = last['authenticating-host'];
    const clientHost = last['client-host'];
    const transport = last.transport;

    const loggedInAs = `Logged in as user '${user}'`;
    const loggedInAt = `Last login at ${loginTime}`;
    const loggedInTo = `to ${authenticatingHost}`;
    const loggedInFrom = `from ${clientHost}`;
    const loggedInTransport = `using ${transport}`;
    return `${loggedInAs}. ${loggedInAt}, ${loggedInTo}, ${loggedInFrom} ${loggedInTransport}`;
};

const getAlarm = (user, rows) => (rows || []).reduce((acc, cur) => {
    if (acc) return acc;
    const numOfFailedLogins = toNumber(cur['num-of-failed-logins']);
    if (numOfFailedLogins > 0) {
        const loginTime = getLoginTime(cur);
        const authenticatingHost = cur['authenticating-host'];
        const clientHost = cur['client-host'];
        const transport = cur.transport;

        const loginFailed = `User '${user}' failed to login ${numOfFailedLogins} times`;
        const loggedInAt = `Most recent failure at ${loginTime}`;
        const loggedInTo = `to ${authenticatingHost}`;
        const loggedInFrom = `from ${clientHost}`;
        const loggedInTransport = `using ${transport}`;
        return (
            `${loginFailed}. ${loggedInAt}, ${loggedInTo}, ${loggedInFrom} ${loggedInTransport}`
        );
    }
    return acc;
}, undefined);


const getDisplayTime = (displayed, rows) => {
    if (displayed) return undefined;
    if (rows === undefined) return undefined;
    if (rows.length === 0) return undefined;
    return 8000;
};

const LastLoginNotificationBar = ({
    useDispatchGetLastLogin,
    useDispatchLastLoginDisplayed,
    useSelectNsoUser,
    useSelectLastLoginDisplayed,
    useSelectLastLoginRows,
}) => {
    const user = useSelectNsoUser();
    const displayed = useSelectLastLoginDisplayed();
    const rows = useSelectLastLoginRows(user);

    const displayTime = getDisplayTime(displayed, rows);

    useDispatchGetLastLogin({ user, displayed });
    useDispatchLastLoginDisplayed({ displayed, displayTime });

    if (displayed) return null;

    if (!rows) return null;
    if (rows.length === 0) return null;

    const notification = getNotification(user, rows);
    const alarm = getAlarm(user, rows);

    return (
        <>
            <NotificationBar message={notification} />
            <NotificationBar message={alarm} type={BAR_TYPES.ALARM} />
        </>
    );
};

LastLoginNotificationBar.propTypes = {
    useDispatchGetLastLogin: PropTypes.func.isRequired,
    useDispatchLastLoginDisplayed: PropTypes.func.isRequired,
    useSelectNsoUser: PropTypes.func.isRequired,
    useSelectLastLoginDisplayed: PropTypes.func.isRequired,
    useSelectLastLoginRows: PropTypes.func.isRequired,
};

LastLoginNotificationBar.defaultProps = {
    useDispatchGetLastLogin: _useDispatchGetLastLogin,
    useDispatchLastLoginDisplayed: _useDispatchLastLoginDisplayed,
    useSelectNsoUser: _useSelectNsoUser,
    useSelectLastLoginDisplayed: _useSelectLastLoginDisplayed,
    useSelectLastLoginRows: _useSelectLastLoginRows,
};

export default LastLoginNotificationBar;
