import React, { useCallback, useEffect, useReducer } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import {
    subscribeChangesAction,
    unsubscribeChangesAction,
} from 'modules/comet/actions';
import { packageUrl } from 'modules/packages/utils';
import { selectWebuiPackages } from 'modules/systemSetting/reducer';
import { read } from 'modules/transactions/actions';
import { useFetchApplications, useFetchUIPackages } from 'modules/hooks/hooks';
import { Messages, translate } from 'modules/intl';
import ApplicationHub from './ApplicationHub';

const hypenToCamel = str => str.replace(/-([a-z])/g, m => m[1].toUpperCase());

const keysToCamel = obj => Object.keys(obj).reduce((a, key) => (
    { ...a, [hypenToCamel(key)]: obj[key] }
), {});

const packageApplication = (pack, category, href, promoLabel) => ({
    id: pack.name,
    title: pack.name,
    category,
    href,
    description: pack.description,
    promoLabel,
    package: true,
});

const selectApplicationCategories = applications =>
    Object.keys(applications).reduce((acc, appKey) => {
        const app = keysToCamel(applications[appKey]);
        const { category } = app;
        const res = acc;
        if (app.hidden === true || app.hidden === 'true') {
            return res;
        }
        if (!(category in acc)) {
            res[category] = {
                key: category,
                title: category,
                applicationItems: [],
            };
        }
        res[category].applicationItems.push(app);
        return res;
    }, {});

const selectPackageCategories = (packages, applications) => {
    const packageCategory = translate(Messages.APPLICATIONHUB.PACKAGES);
    return Object.keys(packages).sort().reduce((acc, packageKey) => {
        const res = acc;
        const pack = packages[packageKey];
        const url = packageUrl(pack.name);
        if (pack.webui !== true) {
            return res;
        }
        const application = Object.entries(applications).find(([name, app]) => (
            name === pack.name && app.href === url
        ));
        if (application) {
            return res;
        }
        if (!(packageCategory in acc)) {
            res[packageCategory] = {
                key: packageCategory,
                title: packageCategory,
                applicationItems: [],
            };
        }
        res[packageCategory].applicationItems.push(
            packageApplication(
                pack,
                packageCategory,
                packageUrl(pack.name),
                translate(Messages.APPLICATIONHUB.PACKAGE_PROMOLABEL),
            ),
        );
        return res;
    }, {});
};

const selectApplicationHubCategories = (applications, packages) => {
    const categories = {
        ...selectApplicationCategories(applications),
        ...selectPackageCategories(packages, applications),
    };

    return Object.keys(categories).map(k => categories[k]);
};

const appPath = '/webui:webui/webui-one:applications/application';
const isAppPath = actionPath => (
    actionPath === appPath
    || actionPath.startsWith(`${appPath}{`)
);

export function ApplicationHubContainer({
    onGetTh,
    webuiPackages,
    onSubscribe,
    onUnsubscribe,
}) {
    const [trigger, incrementTrigger] = useReducer(t => t + 1, 0);
    const packageState = useFetchUIPackages({
        getLastTh: onGetTh,
        packageAppNames: webuiPackages,
    });
    const applicationState = useFetchApplications({
        getLastTh: onGetTh,
        trigger,
    });

    const onCometUpdate = (notification) => {
        const operations = ['created', 'deleted', 'value_set'];

        const shouldUpdateApplications = notification.changes
            .some(({ op, keypath }) => (operations.includes(op) && isAppPath(keypath)), false);

        if (shouldUpdateApplications) {
            incrementTrigger();
        }
    };

    useEffect(() => {
        onSubscribe([appPath], onCometUpdate);

        return () => {
            onUnsubscribe([appPath], onCometUpdate);
        };
    }, [onSubscribe, onUnsubscribe]);

    const selectCategories = useCallback(() => {
        const applications = applicationState.data;
        const packages = packageState.data || {};

        return selectApplicationHubCategories(applications, packages);
    }, [packageState.data, applicationState.data]);

    return (
        <ApplicationHub
            isLoading={applicationState.isLoading}
            categories={selectCategories()}
            isLoadingPackageApplications={packageState.isLoading}
        />
    );
}

ApplicationHubContainer.propTypes = {
    webuiPackages: PropTypes.arrayOf(PropTypes.string),
    onGetTh: PropTypes.func,
    onSubscribe: PropTypes.func,
    onUnsubscribe: PropTypes.func,
};

const mapStateToProps = state => ({
    webuiPackages: selectWebuiPackages(state),
});

const mapDispatchToProps = dispatch => ({
    async onGetTh({ signal }) {
        const { th } = await dispatch(read({ signal }));
        return th;
    },
    onSubscribe(paths, handler) {
        dispatch(subscribeChangesAction(paths, handler));
    },
    onUnsubscribe(paths, handler) {
        dispatch(unsubscribeChangesAction(paths, handler));
    },
});

export default connect(
    mapStateToProps,
    mapDispatchToProps,
)(ApplicationHubContainer);

export {
    mapStateToProps as _mapStateToProps,
    selectApplicationHubCategories as _selectApplicationHubCategories,
};
