import {
    useEffect,
    useRef,
} from 'react';

import { isNumber } from '@webui-one/core/utils/number';

const useTimeout = (
    callback,
    delay,
    {
        setTimeoutFunc = setTimeout,
        clearTimeoutFunc = clearTimeout,
    } = {},
) => {
    const ref = useRef();

    useEffect(() => {
        ref.current = callback;
    }, [callback]);

    useEffect(() => {
        if (!isNumber(delay)) return () => null;
        const id = setTimeoutFunc(() => ref.current(), delay);
        return () => clearTimeoutFunc(id);
    }, [delay, setTimeoutFunc, clearTimeoutFunc]);
};

export default useTimeout;
